.selector-flex-content {
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: center;
    padding-top: 1%;
    padding-right: 1%;
    padding-left: 1%;
  }

.item_buttons {
    display: flex;
    justify-content: space-between;
    padding-top: 2%;
    padding-right: 5%;
    padding-bottom: 2%;
    padding-left: 5%;
    width: 100%;
    background-color: #F7F7F7;
}

.selector-fonts {
  font-family: 'NunitoSans-Light';
  background-color: #F7F7F7;
  height: 100vh;
  margin: 0;
}

button:disabled {
  color: grey;
  opacity: 0.5;
  cursor: default;
}

button:active{
  background-color: #72858C;
}

button:hover {
  background-position: right center; /* change the direction of the change here */
  color: #222e35;
  text-decoration: none;
}


del {
	color: red;
	text-decoration: line-through;
}

.gallery {
  padding: 2%;
  min-height: 1px;
  height:450px;
  width: 80%;
  border: 1px solid #ddd;
  overflow: auto;
  text-align: center;
  background: white;
  overflow: scroll;
  overflow-x: hidden;
}

img:hover {
  box-shadow: 0 0 2px 1px rgba(8, 31, 105, 0.5);
}


@keyframes glowing {
  from {background-color: rgb(192, 189, 189);}
  to {background-color: rgb(21, 255, 0); box-shadow: 0 0 2px #2196f3, 0 0 10px #2196f3, 0 0 20px #2196f3;}
  
}

.my-regular-button {
        display: flex;
        background-color: rgb(192, 189, 189);
        flex-flow: column;
        animation-name: glowing;
        animation-duration: 3s;
        animation-delay: 2s;
        animation-timing-function: linear;
        animation-play-state: running;
        animation-iteration-count: infinite;
        animation-direction: alternate;
}

.my-regular-button:hover {
  color: #255784;
  background: #2196f3;
  box-shadow: 0 0 8px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
  transition-delay: 1s;
}

.my-vip-button {
  background: linear-gradient(to right, #F09819 0%, #EDDE5D  51%, #F09819  100%);
  display: flex; 
  flex-flow: column;
  padding-top: 1%; 
}

                                    

